<template>
  <div class="personal">
    <PersonalHeader></PersonalHeader>
    <div class="greyBg">
      <div class="enterBox">
        <div class="title">
          <h2>企业入驻</h2>
        </div>
        <div class="enterContent">
          <el-form :rules="refs" label-width="80px" ref="form" :model="formLabelAlign" >
            <el-form-item label="品牌名" prop="bname">
              <el-input v-model="formLabelAlign.bname" placeholder="请输入品牌名"></el-input>
            </el-form-item>
            <el-form-item label="公司名" prop="cname">
              <el-input v-model="formLabelAlign.cname" placeholder="请输入公司名"></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="name">
              <el-input v-model="formLabelAlign.name" placeholder="请输入您的姓名"></el-input>
            </el-form-item>
            <el-form-item label="注册地址" prop="address">
              <el-input v-model="formLabelAlign.address" placeholder="请输入公司注册地址"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="phone" >
              <el-input v-model="formLabelAlign.phone" placeholder="请输入手机号码"></el-input>
            </el-form-item>
            <el-form-item label="图片验证" prop="img_code">
              <el-input v-model="formLabelAlign.img_code" placeholder="请输入图片验证码"></el-input>
              <img class="picYz" :src="imgCode" @click="getImgCode">
            </el-form-item>
            <el-form-item label="验证码" prop="code">
              <el-input v-model="formLabelAlign.code" placeholder="请输入手机验证码"></el-input>
              <span class="messageBtn" v-if="showCode" @click="getCode">获取验证码</span>
              <span class="messageBtn" v-else>{{outTime}}S后重新获取</span>
            </el-form-item>
            <el-button class="submitBtn" type="primary" @click="onSubmit('formLabelAlign')">提交申请</el-button>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import commonApi from "@/api/common-api"
  import api from "@/api/enter-api"


  export default {
    name: "personal",
    data(){
      return {
        labelPosition: 'right',
        showCode:true,
        outTime: 60,
        imgCode:'',
        refs: {
          bname: [
            {required: true, message: '请输入品牌名', trigger: 'blur'}
          ],
          cname: [
            {required: true, message: '请输入公司名', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入您的姓名', trigger: 'blur'},
          ],
          address: [
            {required: true, message: '请输入公司注册地址', trigger: 'blur'},
          ],

          phone: [
            {required: true, message: '请填写您的手机号', trigger: 'blur'},
            {len: 11, message: '手机号码格式不正确', trigger: 'blur'}
          ],
          img_code: [
            {required: true, message: '请输入图片验证码', trigger: 'blur'},
          ],
          code: [
            {required: true, message: '请输入手机验证码', trigger: 'blur'},
          ],
        },
        formLabelAlign: {
          name: '',
          cname: '',
          bname: '',
          address:'',
          phone:'',
          img_code:'',
          code:''
        }
      }
    },
    created(){
      this.getImgCode();
    },
    methods:{
      getImgCode: function(){
        commonApi.getImgCode().then(data => {
          this.imgCode = data;
        })


      },
      onSubmit(name) {
        this.$refs[name].validate(valid =>{
          if(valid){
            api.submitForm({...this.formLabelAlign}).then(data=>{
              this.$alert('企业入驻信息提交成功，稍后平台会与您联系', '', {
                confirmButtonText: '确定'
              });
              this.$refs['form'].resetFields();
            })
          }
        })



      },
      getCode:function(){
        const {phone,img_code} = this.formLabelAlign;
        const _this = this;
        if(!this.validatePhone(phone)){
          return
        }
        if(!img_code){
          this.$message.error('请输入图片验证码');
          return
        }

        commonApi.getPhoneCode({phone,img_code}).then(data => {
          this.showCode = false;
          let intervalID = setInterval(function(){
            if(_this.outTime == 0){
              _this.showCode = true;
              _this.outTime = 60;
              clearInterval(intervalID)
            }
            _this.outTime--;
          },1000)
          this.$message({message:'验证码发送成功',type:'success'});
        });
      },
      validatePhone: function(phone){
        if (!phone) {
          this.$message.error('请输入手机号码');
          return false;
        }
        if(phone.length != 11){
          this.$message.error('手机号码格式不正确');
          return false;
        }
        return true;
      },
    },
    components: {
      PersonalHeader
    }
  }
</script>

<style scoped type="text/less" lang="less">
  .greyBg{
    width: 100%;
    padding:20px 0 80px;
    background: #F4F5FA;
  }
  .enterBox{
    width: 1200px;
    height: 640px;
    margin: 0 auto;
    background: #fff;
    .title{
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      margin-bottom: 40px;
      h2{
        width: 66px;
        height: 58px;
        line-height: 58px;
        border-bottom: 2px solid #4F74F4;
        font-size: 16px;
        color: #333;
        text-align: center;
        margin-left: 33px;
      }
    }
    .enterContent{
      width: 400px;
      padding-left: 40px;
    }
    .picYz{
      width: 105px;
      height: 38px;
      position: absolute;
      right: 1px;
      top: 1px;
      z-index: 10;
      cursor: pointer;
    }
    .messageBtn{
      width: 105px;
      height: 22px;
      line-height: 22px;
      border-left: 1px solid #E3E4E5;
      text-align: center;
      position: absolute;
      right: 0;
      top: 8px;
      color: #5389F7;
      cursor: pointer;
    }
    .submitBtn{
      margin: 20px 0 0 81px;
    }
  }




</style>



